  to {
    transform: scale(1); } }

@keyframes zoomout {
  from {
    transform: scale(1); }
  to {
    transform: scale(0.5); } }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@keyframes spin2 {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(720deg); } }

@keyframes spin3 {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(1080deg); } }

@keyframes spin4 {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(1440deg); } }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeout {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

