.content-form input[type='checkbox'] {
  margin-bottom: 5px;
  border: 1px solid white;
  width: 16px;
  height: 16px;
  background: black;
  border-radius: 3px; }
  .content-form input[type='checkbox']:checked {
    background: black; }
    .content-form input[type='checkbox']:checked:before {
      top: -5px;
      left: 2px;
      color: #f05931; }
  .content-form input[type='checkbox'] + label {
    margin-bottom: 5px; }

.content-form .label {
  margin-bottom: 5px; }

.content-form .switch input[type=checkbox] {
  position: absolute;
  left: -9999em;
  padding: 0;
  height: 0;
  width: 0;
  visibility: hidden; }
  .content-form .switch input[type=checkbox] + label {
    cursor: pointer;
    display: block;
    margin-left: 1px;
    margin-bottom: 5px;
    width: 40px;
    height: 20px;
    text-indent: -9999px;
    background: black;
    border: 1px solid white;
    border-radius: 3px;
    position: relative; }

.content-form .switch label:after {
  content: '';
  position: absolute;
  top: 1px;
  left: 2px;
  width: 16px;
  height: 16px;
  background: #f05931;
  border-radius: 2px;
  transition: 0.3s; }

.content-form .switch input:checked + label:after {
  left: calc(100% - 2px);
  transform: translateX(-100%); }

.content-form .switch label:active:after {
  width: 16px; }

.content-form .switch body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

